import React, { useState } from "react"; // useState 
import { Modal, Button, Form } from "react-bootstrap";
import { useDispatch } from "react-redux";
import { updateNotice } from "../../../features/notice/noticeSlice"; // 'editNotice'  'updateNotice'

const EditNoticeDialog = ({ show, handleClose, notice }) => {
  const dispatch = useDispatch();
  const [formData, setFormData] = useState(notice || { title: "", content: "" });

  const handleChange = (event) => {
    const { id, value } = event.target;
    setFormData({ ...formData, [id]: value });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    dispatch(updateNotice({ ...formData, id: notice._id })); // 'editNotice'  'updateNotice'
    handleClose();
  };

  return (
    <Modal show={show} onHide={handleClose}>
      <Modal.Header closeButton>
        <Modal.Title>Edit Notice</Modal.Title>
      </Modal.Header>
      <Modal.Body>
        <Form onSubmit={handleSubmit}>
          <Form.Group controlId="title">
            <Form.Label>Title</Form.Label>
            <Form.Control
              type="text"
              placeholder="Enter title"
              value={formData.title}
              onChange={handleChange}
              required
            />
          </Form.Group>
          <Form.Group controlId="content">
            <Form.Label>Content</Form.Label>
            <Form.Control
              as="textarea"
              rows={3}
              placeholder="Enter content"
              value={formData.content}
              onChange={handleChange}
              required
            />
          </Form.Group>
          <Button variant="primary" type="submit">
            Edit
          </Button>
        </Form>
      </Modal.Body>
    </Modal>
  );
};

export default EditNoticeDialog;

