import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { Container, Button } from "react-bootstrap";
import api from "../../utils/api"; // API  
import Footer from "../../common/component/Footer"; // Footer  import
import "./NoticeDetailPage.style.css"; //   import

const NoticeDetailPage = () => {
  const { id } = useParams(); // URL  ID 
  const navigate = useNavigate();
  const [notice, setNotice] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    //     
    const fetchNoticeDetail = async () => {
      try {
        const response = await api.get(`/notices/${id}`); // api   GET 
        setNotice(response.data); //    
      } catch (err) {
        setError("  .");
      } finally {
        setLoading(false); //   false 
      }
    };

    fetchNoticeDetail();
  }, [id]);

  if (loading) return <p> ...</p>;
  if (error) return <p>{error}</p>;

  return (
    <>
      <Container className="mt-4">
        <h1 className="text-center mb-4"> </h1>
        {notice && (
          <div className="notice-detail">
            <div className="notice-item">
              <strong>:</strong>
              <p>{notice.title}</p>
            </div>
            <div className="notice-item">
              <strong>:</strong>
              <p>{notice.content}</p>
            </div>
            <div className="notice-item">
              <strong> :</strong>
              <p>{new Date(notice.date).toLocaleDateString()}</p>
            </div>
            <Button variant="primary" onClick={() => navigate(-1)}>
              
            </Button>
          </div>
        )}
      </Container>

      {/* Footer    */}
      <Footer />
    </>
  );
};

export default NoticeDetailPage;


