import React, { useState, useEffect } from "react";
import { Container, Button } from "react-bootstrap";
import { useDispatch, useSelector } from "react-redux";
import ReactPaginate from "react-paginate";
import SearchBox from "../../common/component/SearchBox";
import ProductTable from "./component/ProductTable";
import CreateNoticeDialog from "./component/CreateNoticeDialog";
import EditNoticeDialog from "./component/EditNoticeDialog";
import {
  fetchNotices,
  deleteNotice,
} from "../../features/notice/noticeSlice";

const AdminNoticePage = () => {
  const dispatch = useDispatch();
  const notices = useSelector((state) => state.notice.notices);
  const [showCreateDialog, setShowCreateDialog] = useState(false);
  const [showEditDialog, setShowEditDialog] = useState(false);
  const [selectedNotice, setSelectedNotice] = useState(null);
  const [searchQuery, setSearchQuery] = useState({
    page: 1,
    title: "",
  });
  const totalPageNum = useSelector((state) => state.notice.totalPageNum);

  const tableHeader = ["#", "Title", "Content", "Date", "Actions"];

  useEffect(() => {
    dispatch(fetchNotices(searchQuery));
  }, [dispatch, searchQuery]);

  const handleClickCreateNotice = () => {
    setShowCreateDialog(true);
  };

  const handleEditNotice = (notice) => {
    setSelectedNotice(notice);
    setShowEditDialog(true);
  };

  const handleDeleteNotice = (noticeId) => {
    if (window.confirm("Are you sure you want to delete this notice?")) {
      dispatch(deleteNotice(noticeId));
    }
  };

  const handlePageClick = ({ selected }) => {
    setSearchQuery({ ...searchQuery, page: selected + 1 });
  };

  return (
    <div className="locate-center">
      <Container>
        <div className="mt-2 mb-2">
          <SearchBox
            searchQuery={searchQuery}
            setSearchQuery={setSearchQuery}
            placeholder="Search by title"
            field="title"
            className="search-box-custom"
          />
        </div>
        <Button className="mt-2 mb-2" onClick={handleClickCreateNotice}>
          Add New Notice +
        </Button>

        <ProductTable
          header={tableHeader}
          data={notices}
          deleteItem={(notice) => handleDeleteNotice(notice._id)}
          openEditForm={handleEditNotice}
        />
        <ReactPaginate
          nextLabel="next >"
          onPageChange={handlePageClick}
          pageRangeDisplayed={5}
          pageCount={totalPageNum}
          forcePage={searchQuery.page - 1}
          previousLabel="< previous"
          pageClassName="page-item"
          pageLinkClassName="page-link"
          previousClassName="page-item"
          previousLinkClassName="page-link"
          nextClassName="page-item"
          nextLinkClassName="page-link"
          breakLabel="..."
          breakClassName="page-item"
          breakLinkClassName="page-link"
          containerClassName="pagination"
          activeClassName="active"
        />
      </Container>

      <CreateNoticeDialog
        show={showCreateDialog}
        handleClose={() => setShowCreateDialog(false)}
      />

      <EditNoticeDialog
        show={showEditDialog}
        handleClose={() => setShowEditDialog(false)}
        notice={selectedNotice}
      />
    </div>
  );
};

export default AdminNoticePage;






