import React, { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { fetchNotices } from "../../features/notice/noticeSlice";
import { useNavigate } from "react-router-dom";
import { Container, Row, Col, Table, Form } from "react-bootstrap";
import Cbutton from "../../common/component/Cbutton";
import Footer from "../../common/component/Footer"; // Footer  import
import "./NoticePage.style.css";

const NoticePage = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [searchText, setSearchText] = useState("");
  const [option, setOption] = useState("title");
  const { notices } = useSelector((state) => state.notice);

  useEffect(() => {
    dispatch(fetchNotices());
  }, [dispatch]);

  const handleChange = (e) => {
    setSearchText(e.target.value);
  };

  const selectHandle = (e) => {
    setOption(e.target.value);
  };

  const onSubmit = (e) => {
    e.preventDefault();
    //   
  };

  return (
    <>
      <Container className="mt-4">
        <h1 className="text-center mb-4"></h1>
        <Form method="get" onSubmit={onSubmit} className="mb-4">
          <Row className="align-items-center">
            <Col xs={3}>
              <Form.Select value={option} onChange={selectHandle}>
                <option value="title"></option>
                <option value="body"></option>
                <option value="title_body">+</option>
              </Form.Select>
            </Col>
            <Col xs={6}>
              <Form.Control
                type="text"
                name="searchText"
                onChange={handleChange}
                value={searchText}
                placeholder=" "
              />
            </Col>
            <Col xs={3} className="text-end">
              <Cbutton type="submit"></Cbutton>
            </Col>
          </Row>
        </Form>
        <Table striped bordered hover>
          <thead>
            <tr>
              <th></th>
              <th className="notice-date-column"> </th>
            </tr>
          </thead>
          <tbody>
            {notices.length > 0 ? (
              notices.map((notice) => (
                <tr
                  key={notice._id}
                  onClick={() => navigate(`/notice/${notice._id}`)}
                >
                  <td className="notice-title-column">{notice.title}</td>
                  <td className="notice-date-column">
                    {new Date(notice.date).toLocaleString()}
                  </td>
                </tr>
              ))
            ) : (
              <tr>
                <td colSpan="3" className="text-center">
                    .
                </td>
              </tr>
            )}
          </tbody>
        </Table>
      </Container>

      {/* Footer    */}
      <Footer />
    </>
  );
};

export default NoticePage;











